
Template.__checkName("twiitPageNew");
Template["twiitPageNew"] = new Template("Template.twiitPageNew", (function() {
  var view = this;
  return HTML.DIV({
    class: "tweetfeed-container"
  }, "\n    ", HTML.DIV({
    class: "panel panel-default tweetfeed"
  }, "\n      ", HTML.DIV({
    class: "panel-body"
  }, "\n        ", HTML.Raw("<!-- Texto para el contenido del Twitt -->"), "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("countNotifTwiit"));
  }, function() {
    return [ "\n          ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("tweetMessage"));
    }, function() {
      return [ "\n            ", Blaze.View("lookup:saveIdInArr", function() {
        return Spacebars.mustache(view.lookup("saveIdInArr"));
      }), "\n            ", HTML.DIV({
        class: "panel panel-info"
      }, "\n              ", HTML.DIV({
        class: "panel-heading"
      }, "\n                ", HTML.H3({
        class: "panel-title"
      }, "@", Blaze.View("lookup:..twiitNotifUserName", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "twiitNotifUserName"));
      }), " \n                  ", HTML.SPAN({
        class: "glyphicon glyphicon-triangle-right",
        "aria-hidden": "true"
      }), "\n                   ", Blaze.View("lookup:convertDateTime", function() {
        return Spacebars.mustache(view.lookup("convertDateTime"));
      }), "\n                 "), "\n              "), "\n              ", HTML.DIV({
        class: "panel-body"
      }, "\n                ", Blaze.View("lookup:..twiitMessage", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "twiitMessage"));
      }), "\n              "), "\n               ", HTML.DIV({
        class: "btn-group",
        role: "group",
        id: "btnGroupInteractions"
      }, "\n                ", HTML.BUTTON({
        type: "button",
        class: "btn btn-secondary"
      }, "\n                  ", HTML.SPAN({
        class: "glyphicon glyphicon-bullhorn"
      }), "\n                "), "\n\n                ", HTML.BUTTON({
        type: "button",
        id: "btnComm",
        class: "btn btn-secondary",
        "data-toggle": "modal",
        "data-target": "#dialog-NewTwiit"
      }, "\n                  ", HTML.SPAN({
        class: "glyphicon glyphicon-comment"
      }), "\n                  ", HTML.SPAN({
        class: "badge-numFav badge"
      }, Blaze.View("lookup:..numComment", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "numComment"));
      })), "\n                "), "\n                \n                ", HTML.BUTTON({
        type: "button",
        id: "btnFav",
        class: "btn btn-secondary"
      }, "\n                  ", HTML.SPAN({
        class: "glyphicon glyphicon-heart"
      }), "\n                  ", HTML.SPAN({
        class: "badge-numFav badge"
      }, Blaze.View("lookup:..numFav", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "numFav"));
      })), "\n                "), "\n              "), "\n            "), "\n          " ];
    }), "\n           ", HTML.BUTTON({
      id: "btnDismissNotif",
      class: "btn btn-info pull-right",
      type: "button"
    }, "OK"), "\n        " ];
  }, function() {
    return [ "\n          ", HTML.DIV({
      class: "panel-info"
    }, "\n            ", HTML.P("Oooooooops Twiit Page New"), "    \n          "), "\n        " ];
  }), "\n      "), "\n    "), "\n  ");
}));
